:root {
  --primary-color: #3498db;
  --primary-color-dark: #2980b9;
  --secondary-color: #2ecc71;
  --secondary-color-dark: #27ae60;
  --background-color: #f5f7fa;
  --text-color: #34495e;
  --card-background: #ffffff;
}

body {
  font-family: 'Arial', sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
}

.App {
  max-width: 1200px;
  margin: 0 auto;
  padding: 2rem;
}

.header {
  text-align: center;
  margin-bottom: 2rem;
}

.header h1 {
  font-size: 2.5rem;
  color: var(--primary-color);
}

.container {
  display: flex;
  gap: 2rem;
}

.schedule-section, .call-roster-section {
  flex: 1;
  background-color: var(--card-background);
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
}

.calendar-container {
  font-size: 0.9rem;
}

.react-calendar {
  border: none;
  width: 100%;
}

.react-calendar__tile {
  padding: 1em 0.5em;
}

.react-calendar__tile--now {
  background: var(--primary-color);
  color: white;
}

.react-calendar__tile--active {
  background: var(--secondary-color);
  color: white;
}

.react-calendar__month-view__weekdays__weekday {
  color: var(--primary-color);
}

.react-calendar__month-view__days__day--weekend {
  color: var(--text-color);
}

.resident-info {
  background-color: #f1f8ff;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  margin-bottom: 0.5rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

button {
  border: none;
  border-radius: 4px;
  padding: 0.5rem 1rem;
  cursor: pointer;
  transition: background-color 0.3s;
  margin-right: 0.5rem;
}

button:last-child {
  margin-right: 0;
}

.remove-button {
  background-color: #e74c3c;
  color: white;
}

.generate-button {
  background-color: var(--secondary-color);
  color: white;
}

.clear-button {
  background-color: #e67e22;
  color: white;
}

.summary-section {
  margin-top: 2rem;
  background-color: var(--card-background);
  border-radius: 8px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  padding: 1.5rem;
}

@media (max-width: 768px) {
  .App {
    padding: 1rem;
  }

  .container {
    flex-direction: column;
  }

  .schedule-section, .call-roster-section {
    width: 100%;
  }

  .header h1 {
    font-size: 2rem;
  }

  button {
    width: 100%;
    margin-right: 0;
    margin-bottom: 0.5rem;
  }

  .shift-summary table, .shift-summary th, .shift-summary td {
    display: block;
    width: 100%;
  }

  .shift-summary th, .shift-summary td {
    box-sizing: border-box;
  }
}

.copyright {
  margin-top: 20px;
  font-size: 0.8rem;
  color: #666;
}

label {
  margin-right: 10px;
}

input {
  width: 100%;
  padding: 0.5rem;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 1rem;
  margin-bottom: 10px;
  margin-right: auto;
}

.form-container {
  margin: 1rem;
}